            p Cuando se habla de calidad en alimentos o en otro tipo de bienes, se refiere a la inspección del producto elaborado, siendo necesario desechar aquellos productos que se encuentran terminados o aún en elaboración por ser considerados defectuosos, teniendo como referencia las características inspeccionadas. Cabe resaltar que desechar un producto por completo implica un alto costo para la empresa, ya que se invirtió tiempo, desgaste de las maquinarias, así como materias primas e insumos que se utilizaron para llegar hasta ese punto.
