      .col-lg-9.my-3
        p(data-aos="fade-down") La calidad es el “grado en el que un conjunto de características inherentes cumple con las necesidades o expectativas establecidas, implícitas u obligatorias” (Cortés, 2017, 10).    
        .cajon.color-primario.p-4(data-aos="flip-down") 
          p(data-aos="fade-down").mb-0 Es decir, la calidad exige que se identifiquen, establezcan y cumplan unos criterios o medidas específicas, que atiendan a los requerimientos de los individuos o clientes, teniendo en cuenta que la calidad debe ser fácil de determinar por uno u otro inspector.

    .row.justify-content-center.align-items-center.mb-4 
      .col-lg-7.my-3
        p(data-aos="fade-down") Con el siguiente ejemplo se puede tener mayor claridad sobre el concepto de calidad:
        p(data-aos="fade-down") En una planta dedicada al empaquetado de azúcar se generan 3 tipos de presentación: azúcar de 500 gramos, de 1 Kg y 3 Kg. Para la inspección de calidad se decide que uno de los elementos a medir en el producto terminado va a ser la cantidad de azúcar de acuerdo a la presentación, por lo que se toman 10 bolsas de la presentación de 500 gramos y se revisa su peso; para esto se utiliza una balanza, por lo que el dato no va a variar de acuerdo al inspector y se va a poder determinar si la bolsa cumple o no cumple con los 500 gramos.

        p(data-aos="fade-down") Cabe destacar que en este tipo de situaciones también se establece un margen que permite determinar si el peso está o no dentro de lo establecido. Por ejemplo, para la bolsa de 500 gramos se puede determinar que esta va a cumplir si al llevarla a la balanza está entre 497 g y 503 g, es decir, un margen de 3 gramos por arriba o por debajo. Este tipo de consideraciones deben darse antes de iniciarse el proceso de medición, si la tolerancia va a ser únicamente para valores superiores, los instrumentos a utilizar deben encontrarse en óptimas condiciones, se deben respetar las normas o leyes que regulen el tipo de producto, y todos los elementos que permitan ofrecer productos de calidad y eficiencia en los costos.                         
