        p.mb-0 Las enfermedades transmitidas por alimentos (ETA) se originan por la ingesta de alimentos, además de agua, que contengan diferentes agentes etiológicos, en ciertas cantidades que afecten la salud de los consumidores, ya sea a nivel individual o a un colectivo de personas. Además, las enfermedades que se relacionan con el consumo de agua son las que se producen por agua contaminada con diferentes tipos de desechos humanos, de origen animal o químicos. Entre las enfermedades que se transmiten por aguas contaminadas están la fiebre tifoidea, la poliomielitis, la disentería, la meningitis y la hepatitis A.

    p(data-aos="fade-down").mb-5.text-center Los factores que propician el escenario idóneo para la transmisión de enfermedades por alimentos son: 

