      .col-lg-10.my-3
        p(data-aos="fade-down") Por otra parte, es fundamental aprender a identificar los síntomas que generan las enfermedades transmitidas por alimentos, los cuales se manifiestan como una enfermedad gastrointestinal, y entre los que predominan las náuseas, vómitos, dolor estomacal y diarrea. Sin embargo, no se puede menospreciar este tipo de enfermedades, ya que pueden llegar a causar síntomas neurológicos e inmunológicos. Por lo general, un brote de enfermedades transmitidas por alimentos se presenta de forma masiva en instituciones educativas, centros de reclusión o lugares donde se proporcionan y preparan alimentos en grandes cantidades. 

